.button {
  display: block;
  width: 150px;
  padding: 0.975rem 0.75rem;
  font-size: 1rem;
  /* margin: 2rem 24rem; */
  margin-left: 170px;
  margin-top: 20px;
  text-decoration: none;
  background-color: transparent;
  border-radius: 7px;
  box-shadow: 0 0 40px 40px rgba(158, 158, 158, 0.75) inset,
    0 0 0 0 rgba(158, 158, 158, 0.75);
  font-weight: bold;
  color: rgb(28, 24, 24);
  transition: 0.15s ease-in-out;
}

.button:hover {
  box-shadow: 0 0 10px 0 rgba(158, 158, 158, 0.75) inset,
    0 0 10px 4px rgba(158, 158, 158, 0.75);
  color: rgba(35, 32, 32, 0.85);
}
